  border-right: 1px red solid;
  border-bottom: 1px red solid;
}

.dh-history-inner li {
  vertical-align: top;
  width: 300px;
  padding-left: 20px;
  float: left;
  color: red;
  border-right: 1px #eee solid;
}
.btn-prev,
.btn-next {
  position: absolute;
  top: 0;
