.btn-prev {
  left: 0;
}
.btn-next {
  right: 0;
}
</style>

<template>
  <div class="dh-history w1160">
    <div class="dh-history-box">
      <div class="btn-prev" @click="prevClick">&lt;</div>
      <div class="btn-next" @click="nextClick">&gt;</div>
      <ul class="dh-history-nav clearfix" :style="`transform:translateX(${historyNavLeft}px)`">
        <li>导航1</li>
        <li>导航2</li>
        <li>导航3</li>
        <li>导航4</li>
        <li>导航5</li>
        <li>导航6</li>
        <li>导航7</li>
      </ul>

      <ul class="dh-history-inner clearfix" :style="`transform:translateX(${historyInnerLeft}px)`">
        <li style="height:500px;">内容1</li>
        <li style="height:200px;">内容2</li>
        <li style="height:400px;">内容3</li>
        <li style="height:200px;">内容4</li>
        <li style="height:100px;">内容5</li>
        <li style="height:600px;">内容6</li>
        <li style="height:300px;">内容7</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "history",
  props: {
    msg: String
  },
  data(){
    return {
        historyNavLeft: 0,
      historyInnerLeft: 0
    }
  },
  methods: {
    prevClick() {
      this.historyNavLeft += 250;
      this.historyInnerLeft += 250;
      if (this.historyNavLeft > 0) {
        this.historyNavLeft = 0;
        this.historyInnerLeft = 0;
      }
    },
    nextClick() {
      this.historyNavLeft -= 250;
      this.historyInnerLeft -= 250;
      if (this.historyNavLeft < -1100) {
        this.historyNavLeft = -1100;
        this.historyInnerLeft = -1100;
      }
    }
  }
};
</script>

