<script>
// @ is an alias to /src
import history from "@/components/history";
export default {
  name: "index",
  components: {
    history
  },
  data() {
    return {
      recommendData: null,
      favorite: null,
      benji: null,
      xiaji: null
    };
  },
  created() {
    this.getRecommend();
    this.getAnyTypeVideoInfo(1);
    this.getAnyTypeVideoInfo(2);
    this.getAnyTypeVideoInfo(3);
  },
  methods: {
    asd() {
      this.$message.success("感谢有你， 😄");
    },
    async getRecommend() {
      let { data } = await axios.get("/index/getRecommend");
      if (data.status == 0) {
        this.recommendData = data.data;
      }
    },
    async getAnyTypeVideoInfo(type) {
      let { data } = await axios.get("/index/getAnyTypeVideoInfo", {
        params: {
          type
        }
      });
      if (data.status == 0) {
        switch (type) {
          case 1:
            this.xiaji = data.data;
            break;
          case 2:
            this.benji = data.data;
            break;
          case 3:
            this.favorite = data.data;
            break;
        }
      }
    }
  }
};
</script>
